import {
    A,
    deg,
    degPerSec,
    degPerSecSq,
    g,
    kg,
    kgM,
    kgMPerSec,
    m,
    mA,
    mm,
    mmPerSec,
    mmPerSecSq,
    mPerSec,
    mPerSecSq,
    ms,
    mV,
    N,
    rad,
    radPerSec,
    radPerSecSq,
    s,
    Unit,
    V,
    W,
    kgMSq,
    degreesCelsius,
} from '@universal-robots/utilities-units';
import { AllUnits } from './value';

// This type definition ensures that the below Map is always up-to-date with the public units exposed as value objects
type ValueObjectUnitMap = {
    [unitString in AllUnits]: Unit;
};

/**
 * Map object containing all the ValueObject strings, mapped to their Unit Library unit
 */
export const ValueObjectMap: ValueObjectUnitMap = {
    g,
    kg,
    kgM,
    'kgm/s': kgMPerSec,
    'deg/s': degPerSec,
    'deg/s^2': degPerSecSq,
    'm/s': mPerSec,
    'm/s^2': mPerSecSq,
    'mm/s': mmPerSec,
    'mm/s^2': mmPerSecSq,
    'rad/s': radPerSec,
    'rad/s^2': radPerSecSq,
    A,
    N,
    V,
    deg,
    m,
    mA,
    mV,
    mm,
    ms,
    rad,
    s,
    W,
    'kg*m^2': kgMSq,
    degreesCelsius,
};
