import { OperatorScreen } from '../../types/nodes';
import { EventTargetOrCommChannel, MessageInvokerService } from '../message-invoker';

export const OPERATOR_SCREEN_SERVICE_MESSAGE_TYPE_BASE = 'ur.web.message.OperatorScreenService.';

/**
 * Service for handling updating of operator screen instances
 */
export class OperatorScreenService extends MessageInvokerService {
    /**
     * @constructor
     * @param eventTarget
     */
    constructor(override readonly eventTarget: EventTargetOrCommChannel) {
        super(OPERATOR_SCREEN_SERVICE_MESSAGE_TYPE_BASE, eventTarget);
    }

    /**
     * Update the operator screen instance
     * @param operatorScreen New operator screen instance data
     */
    async updateOperatorScreen(operatorScreen: OperatorScreen): Promise<void> {
        return this.postAndAwait('updateOperatorScreen', { operatorScreen });
    }
}
