import { TabInputModel } from '../../../common';
import { Acceleration, AngularAcceleration, AngularSpeed, Speed } from '../../../values';
import { ProgramNode } from '../../program.node';
import { URVariable } from '../../ur-symbol';
import { WaypointTabInputModel } from './move-to.node';
import { NodeType } from './program-node-type';

export enum OrientationMode {
    FIXED = 'fixed',
    UNCONSTRAINED = 'unconstrained',
}

export interface MoveCNode extends ProgramNode {
    type: NodeType.MOVE_C;
    parameters: {
        motionProfile: string | undefined;
        name: TabInputModel<URVariable>;
        waypoint?: WaypointTabInputModel;
        advanced: MoveCAdvancedSettings;
        mode: OrientationMode;
    };
}

export interface MoveCAdvancedSettings {
    speed: MoveCSpeedSettings;
    transform: MoveCTransformSettings;
}

export interface MoveCSpeedSettings {
    speed: TabInputModel<Speed | AngularSpeed>;
    acceleration: TabInputModel<Acceleration | AngularAcceleration>;
}

export interface MoveCTransformSettings {
    transform: boolean;
    poseVariable?: URVariable;
}
