import { EventTargetOrCommChannel, MessageInvokerService } from '../message-invoker';

export const SYSTEM_SERVICE_MESSAGE_TYPE_BASE = 'ur.web.message.SystemService.';

/**
 * Service that handles system information.
 */
export class SystemService extends MessageInvokerService {
    /**
     * Create a service for the specific worker
     * @param eventTarget
     */
    constructor(override readonly eventTarget: EventTargetOrCommChannel) {
        super(SYSTEM_SERVICE_MESSAGE_TYPE_BASE, eventTarget);
    }

    /**
     * Get the software version.
     * @returns The software version.
     */
    getSoftwareVersion(): Promise<string> {
        return this.postAndAwait('getSoftwareVersion');
    }
}
