import { MomentInertia } from '../../../values';

/** Position */
/**  Constant array of the InertiaMatrixKeys. */
export const InertiaMatrixKeys = ['Ixx', 'Iyy', 'Izz', 'Ixy', 'Ixz', 'Iyz'] as const;
/** Type describing the InertiaMatrixKeys. */
export type InertiaMatrixKey = (typeof InertiaMatrixKeys)[number];

/**
 * Type describing a Moment of Inertia
 */
export type Inertia = Record<InertiaMatrixKey, MomentInertia>;
