import { EventTargetOrCommChannel, MessageInvokerService } from '../message-invoker';

export const PROGRAM_CODE_SERVICE_MESSAGE_TYPE_BASE = 'ur.web.message.ProgramCodeService.';

/**
 * Service that handles code generation
 */
export class ProgramCodeService extends MessageInvokerService {
    /**
     * @constructor
     * @param target
     */
    constructor(override readonly eventTarget: EventTargetOrCommChannel) {
        super(PROGRAM_CODE_SERVICE_MESSAGE_TYPE_BASE, eventTarget);
    }

    /**
     *
     */
    async generateModulesScriptCode(): Promise<string> {
        return this.postAndAwait('GENERATE_MODULES_SCRIPT_CODE', {});
    }

    /**
     *
     */
    async generateGlobalFunctionsScriptCode(): Promise<string> {
        return this.postAndAwait('GENERATE_FUNCTIONS_SCRIPT_CODE', {});
    }

    /**
     *
     */
    async generateModulesAndGlobalFunctionsScriptCode(): Promise<string> {
        return this.postAndAwait('GENERATE_MODULES_AND_FUNCTIONS_SCRIPT_CODE', {});
    }

    /**
     *
     */
    async getProgramPreambleScriptCode(): Promise<string> {
        return this.postAndAwait('GET_PROGRAM_PREAMBLE_SCRIPT_CODE', {});
    }

    /**
     *
     */
    async generateFunctionScriptCode(functionNodeID: string): Promise<string> {
        return this.postAndAwait('GET_FUNCTION_SCRIPT_CODE', functionNodeID);
    }
}
