import { AccelerationUnit, AccelerationUnits } from './acceleration';
import { AngleUnit, AngleUnits } from './angle';
import { AngularAccelerationUnit, AngularAccelerationUnits } from './angular-acceleration';
import { AngularSpeedUnit, AngularSpeedUnits } from './angular-speed';
import { CurrentUnit, CurrentUnits } from './current';
import { ForceUnit, ForceUnits } from './force';
import { LengthUnit, LengthUnits } from './length';
import { MassUnit, MassUnits } from './mass';
import { MomentInertiaUnit, MomentInertiaUnits } from './moments-inertia';
import { MomentumUnit, MomentumUnits } from './momentum';
import { SpeedUnit, SpeedUnits } from './speed';
import { TemperatureUnit, TemperatureUnits } from './temperature';
import { TimeUnit, TimeUnits } from './time';
import { VoltageUnit, VoltageUnits } from './voltage';

export const AllUnitStrings: Set<AllUnits> = new Set<AllUnits>([
    ...MassUnits,
    ...SpeedUnits,
    ...LengthUnits,
    ...AngleUnits,
    ...VoltageUnits,
    ...CurrentUnits,
    ...AngularSpeedUnits,
    ...AccelerationUnits,
    ...AngularAccelerationUnits,
    ...TimeUnits,
    ...ForceUnits,
    ...MomentumUnits,
    ...MomentInertiaUnits,
    ...TemperatureUnits,
]);

export type AllUnits =
    | MassUnit
    | SpeedUnit
    | LengthUnit
    | AngleUnit
    | VoltageUnit
    | CurrentUnit
    | AngularSpeedUnit
    | AccelerationUnit
    | AngularAccelerationUnit
    | TimeUnit
    | ForceUnit
    | MomentumUnit
    | MomentInertiaUnit
    | TemperatureUnit;

export interface Value<U extends AllUnits = AllUnits> {
    value: number;
    unit: U;
}

export interface ValueRange<U extends AllUnits = AllUnits> {
    min: number;
    max: number;
    unit: U;
}
