import { SelectedInput, SelectedType, TabInputValue, ValueType } from '@universal-robots/ui-models';
import { ProgramBehaviorAPI } from '../../api-classes';

/**
 * Model to be used in collaboration with tabInputComponent.
 */
export class TabInputModel<T> {
    /**
     * Entity is the possibly complex object that TabInput is representing
     */
    public entity: T;

    /**
     * SelectedType is the type of value that the user has selected: 'Value' | 'Variable' | 'Expression'
     */
    public selectedType: SelectedType;

    /**
     * Value is either the "value" of the "variable" or the "expression" entered by the user
     */
    public value: ValueType;

    /**
     * Initialize the object.
     */
    constructor(entity: T, selectedType: SelectedType, value: ValueType) {
        this.entity = entity;
        this.selectedType = selectedType;
        this.value = value;
    }

    /**
     * Validates the model
     * @param model to validate
     */
    static async isValid<T>(model: TabInputModel<T>) {
        if (model.selectedType === SelectedInput.VARIABLE) {
            const api = new ProgramBehaviorAPI(self);
            const isSuppressed = await api.symbolService.isSuppressed(model.value as string);
            const isRegisteredVariableName = await api.symbolService.isRegisteredVariableName(model.value as string);

            if (!isRegisteredVariableName || isSuppressed) {
                return false;
            }
        }
        return true;
    }

    /**
     * Checks if tabInputModel has an entity and a selectedType
     * @param value to be interrogated
     */
    static isTabInputModel<T>(value: any): value is TabInputModel<T> {
        return value.entity !== undefined && value.selectedType !== undefined;
    }

    /**
     * Returns TabInputValue from TabInputModel which is the input for the TabInput component
     */
    static getTabInputValue<T>(model: TabInputModel<T>): TabInputValue {
        return {
            selectedType: model.selectedType,
            value: model.value,
        };
    }

    /**
     * Sets TabInputValue on TabInputModel which is the input for the TabInput component
     * @param model to update the TabInputValue
     * @param value the inserted value for the model.
     */
    static setTabInputValue<T>(model: TabInputModel<T>, value: TabInputValue): void {
        model.selectedType = value.selectedType;
        model.value = value.value;
    }
}
