/**
 * JointPositions defines a position of the robot, specified
 * by the angle of the joints.
 */
export interface JointPositions {
    base: number;
    shoulder: number;
    elbow: number;
    wrist1: number;
    wrist2: number;
    wrist3: number;
}

/**
 * A type guard to check if an object is a JointPositions
 */
export function isJointPositions(jp: any): jp is JointPositions {
    return (
        (jp as JointPositions).base !== undefined &&
        (jp as JointPositions).shoulder !== undefined &&
        (jp as JointPositions).elbow !== undefined &&
        (jp as JointPositions).wrist1 !== undefined &&
        (jp as JointPositions).wrist2 !== undefined &&
        (jp as JointPositions).wrist3 !== undefined
    );
}
