import {
    CommunicationChannel,
    EventTargetCommunicationChannel,
    isCommunicationChannel,
    RobotMoveService,
    UREventTarget,
} from '../services';
import { CommonPresenterAPI } from './common.api';

/**
 * WebComponentDialogAPI
 */
export class WebComponentDialogAPI extends CommonPresenterAPI {
    /** Creates a new instance of the WebComponentDialogAPI, to allow communication with the
     * surrounding application from within a WebComponent
     *
     * @param target An EventTarget, provided by PolyScope X
     */
    constructor(target: UREventTarget | CommunicationChannel) {
        let communicationChannel;
        if (isCommunicationChannel(target)) {
            communicationChannel = target;
        } else {
            communicationChannel = new EventTargetCommunicationChannel(target as EventTarget);
        }
        super(communicationChannel);
        this.robotMoveService = new RobotMoveService(communicationChannel);
    }

    /**
     * API for working with robot moves, i.e. bringing up the move screen etc.
     */
    robotMoveService: RobotMoveService;
}
