import { NodeType, ProgramNode, URFunction, URModule } from '../..';

export const ThreadAction = {
    KILL: 'Kill',
    RUN: 'Run',
} as const;
export type ThreadAction = (typeof ThreadAction)[keyof typeof ThreadAction];
export interface ThreadNode extends ProgramNode {
    type: NodeType.THREAD;
    parameters: {
        selectedThread?: URFunction;
        selectedModule?: URModule;
        selectedAction?: ThreadAction;
    };
}
