import { SourcesNode } from '../../types/nodes';
import { EventTargetOrCommChannel, MessageInvokerService } from '../message-invoker';

export const SOURCE_NODE_SERVICE_MESSAGE_TYPE_BASE = 'ur.web.message.SourceNodeService.';

/**
 * Service that handles updating source nodes
 */
export class SourceNodeService extends MessageInvokerService {
    /**
     * @constructor
     * @param eventTarget
     */
    constructor(override readonly eventTarget: EventTargetOrCommChannel) {
        super(SOURCE_NODE_SERVICE_MESSAGE_TYPE_BASE, eventTarget);
    }

    /**
     * Update the source node
     * @param node New source node state
     */
    public updateNode(node: SourcesNode): Promise<void> {
        return this.postAndAwait('updateNode', node);
    }
}
