import { Observable } from 'rxjs';
import { EventTargetOrCommChannel, MessageInvokerService } from '../message-invoker';

export const FREEDRIVE_STATE_SERVICE_MESSAGE_TYPE_BASE = 'ur.web.message.FreedriveStateService.';
/**
 * Provides access to freedrive information on the robot
 */
export class FreedriveService extends MessageInvokerService {
    /**
     * @constructor
     * @param eventTarget
     */
    constructor(override readonly eventTarget: EventTargetOrCommChannel) {
        super(FREEDRIVE_STATE_SERVICE_MESSAGE_TYPE_BASE, eventTarget);
    }

    /**
     * Get the freedrive state
     *
     * @returns {Observable<boolean>} An observable that emits `true` if the robot is in freedrive state
     */
    isInFreedrive(): Observable<boolean> {
        return this.postAndObserve('isInFreedrive');
    }
}
