import { JsonSerializable, SerializerImplementation } from 'threads';
import { ApplicationContext, ContributionDefinition } from '../context';
import { ApplicationNode, Frame, FrameName, Pose } from '../nodes';
import { serializableType } from './serializable.types';

/**
 * A definition of the Application Context that is able to be serialized
 */
interface SerializableApplicationContext {
    type: serializableType;
    contributions: Readonly<SerializableContributionContext>;
    frames: Readonly<SerializableFramesContext>;
}

/**
 * A definition of the ContributionContext that is able to be serialized
 */
interface SerializableContributionContext {
    contributionList: Readonly<ContributionDefinition>[];
}

/**
 * A definition of the FramesContext that is able to be serialized
 */
interface SerializableFramesContext {
    framesList: Readonly<SerializableFrame>[];
}

/**
 * A definition of Frame that is able to be serialized
 */
interface SerializableFrame {
    name: Readonly<FrameName>;
    pose: Readonly<Pose>;
    parent?: Readonly<FrameName>;
}

/**
 * Checks that the message coming is in a SerializableApplicationContext
 * @param message The message to serialize
 */
function isSerializedApplicationContext(message: unknown): message is SerializableApplicationContext {
    return (message as boolean) && (message as SerializableApplicationContext).type === '$$ApplicationContext';
}

/**
 * Creates the serializer for the ApplicationContext. Handles serializing and deserializing of the object.
 * @constructor
 */
export const ApplicationContextSerializer = (): SerializerImplementation => {
    return {
        /**
         * deserialize the message using a given handler
         * @param message
         * @param defaultHandler
         */
        deserialize(message, defaultHandler) {
            if (isSerializedApplicationContext(message)) {
                // created the application context from the message
                return {
                    contributions: {
                        contributionList: message.contributions?.contributionList ?? [],
                        /**
                         * Returns a Contribution node base on the param
                         * @param type String specifying the contribution node to fetch
                         */
                        getContribution(type: string): ApplicationNode | undefined {
                            return this.contributionList.find((contribution: ContributionDefinition) => contribution.parentType === type);
                        },
                    },
                    frames: {
                        framesList: message.frames?.framesList ?? [],
                        /**
                         * Returns a Frame base on the param
                         * @param frameId String specifying the frame to fetch
                         */
                        getFrame(frameId: string): Frame | undefined {
                            return this.framesList.find((frame: Frame) => frame.name === frameId);
                        },
                    },
                } as ApplicationContext;
            } else {
                return defaultHandler(message);
            }
        },
        /**
         * serializing the item using a give handler
         * @param item
         * @param defaultHandler
         */
        serialize(item, defaultHandler): JsonSerializable {
            if (isSerializedApplicationContext(item)) {
                return {
                    type: '$$ApplicationContext',
                    contributions: {
                        contributionList: item.contributions?.contributionList ?? [],
                    },
                    frames: {
                        framesList: item.frames?.framesList ?? [],
                    },
                };
            } else {
                return defaultHandler(item);
            }
        },
    };
};
