import { MotionProfile, MotionProfileMoveType } from '../../types/nodes';
import { EventTargetOrCommChannel, MessageInvokerService } from '../message-invoker';

export const MOTION_PROFILES_SERVICE_MESSAGE_TYPE_BASE = 'ur.web.message.MotionProfilesService.';

/**
 * Service for getting information of motion profiles
 */
export class MotionProfilesService extends MessageInvokerService {
    /**
     * @constructor
     * @param eventTarget
     */
    constructor(override readonly eventTarget: EventTargetOrCommChannel) {
        super(MOTION_PROFILES_SERVICE_MESSAGE_TYPE_BASE, eventTarget);
    }

    /**
     * Returns the data stored on the specified Motion Profiles type
     * @param type the type of the Motion Profiles to fetch:
     * - joint
     * - linear
     * - process
     */
    getMotionProfiles(type: MotionProfileMoveType): Promise<MotionProfile[]> {
        return this.postAndAwait('getMotionProfiles', { type });
    }
}
