import { ScriptFile } from '../../types/scriptfile/scriptfile';
import { EventTargetOrCommChannel, MessageInvokerService } from '../message-invoker';

export const SCRIPT_FILE_SERVICE_MESSAGE_TYPE_BASE = 'ur.web.message.ScriptFileService.';

/**
 * Service for getting script files
 */
export class ScriptFileService extends MessageInvokerService {
    /**
     * @constructor
     * @param eventTarget
     */
    constructor(override readonly eventTarget: EventTargetOrCommChannel) {
        super(SCRIPT_FILE_SERVICE_MESSAGE_TYPE_BASE, eventTarget);
    }

    /**
     * Returns the script files that are available. This will not contain the urscript content
     */
    getScriptFiles(): Promise<ScriptFile[]> {
        return this.postAndAwait('getScriptFiles');
    }

    /**
     * Returns a script file for a given name, including the urscript content
     * @param name
     */
    getScriptFile(name: string): Promise<ScriptFile> {
        return this.postAndAwait('getScriptFile', name);
    }
}
