import { TabInputModel } from '../../../common';
import { ProgramNode } from '../../program.node';
import { URVariable } from '../../ur-symbol';
import { NodeType } from './program-node-type';

export enum ActionEnum {
    START = 'Start',
    PAUSE = 'Pause',
    RESET = 'Reset',
}

export type ActionType = ActionEnum;

export interface TimerNode extends ProgramNode {
    type: NodeType.TIMER;
    parameters: {
        variable: TabInputModel<URVariable>;
        action: ActionEnum;
    };
}
