import { firstValueFrom, Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { SmartSkillInstance } from '../types/nodes';
import { EventTargetOrCommChannel, MessageInvokerService } from './message-invoker';

export const SMART_SKILL_INSTANCE_SERVICE_MESSAGE_TYPE_BASE = 'ur.web.message.SmartSkillInstanceService.';

/**
 * Service that handles updating smart skill instances
 */
export class SmartSkillInstanceService extends MessageInvokerService {
    /**
     * @constructor
     * @param eventTarget
     */
    constructor(override readonly eventTarget: EventTargetOrCommChannel) {
        super(SMART_SKILL_INSTANCE_SERVICE_MESSAGE_TYPE_BASE, eventTarget);
    }

    /**
     * Update the program node
     * @param instance New smart skill instance data
     */
    async updateInstance(instance: SmartSkillInstance): Promise<void> {
        return this.postAndAwait('updateInstance', {
            instance,
        });
    }

    /**
     * Get all smart skill instances as an observable stream.
     * @returns Observable<SmartSkillInstance[]>
     */
    getInstances(): Observable<SmartSkillInstance[]> {
        return this.postAndObserve('getAllInstancesObservable', {});
    }

    /**
     * Get the smart skill instance by type and optionally name. If name is not provided, the first instance of the type is returned.
     * @param type Type of the smart skill instance
     * @param name Optional name of the smart skill instance
     */
    async getInstance(type: string, name?: string): Promise<SmartSkillInstance | undefined> {
        return firstValueFrom(
            this.getInstances().pipe(
                map((instances) => instances.find((instance) => instance.type === type && (!name || instance.name === name)) || undefined),
            ),
        );
    }
}
