import {
    CommunicationChannel,
    EventTargetCommunicationChannel,
    FramesService,
    isCommunicationChannel,
    ProgramCodeService,
    TreeBuilder,
    UREventTarget,
    WorkerCommunicationChannel,
    WorkerScopeInterface,
} from '../services';
import { RobotMoveService, SmartSkillInstanceService } from '../services';
import { FreedriveService } from '../services/robot/freedrive.service';
import { CommonBehaviorAPI, CommonPresenterAPI } from './common.api';

/**
 * Provides access to Smart Skills related API features in BehaviorWorkers
 */
export class SmartSkillsBehaviorAPI extends CommonBehaviorAPI {
    /** Creates a new instance of the SmartSkillsBehaviorAPI to allow communication with the
     * surrounding application from within a WebWorker
     *
     * @param target A worker, provided with 'self' when created inside a web worker
     */
    constructor(target: UREventTarget | CommunicationChannel) {
        let communicationChannel;
        if (isCommunicationChannel(target)) {
            communicationChannel = target;
        } else {
            communicationChannel = new WorkerCommunicationChannel(target as WorkerScopeInterface);
        }
        super(communicationChannel);
        this.builder = new TreeBuilder(communicationChannel);
        this.programCode = new ProgramCodeService(communicationChannel);
        this.framesService = new FramesService(communicationChannel);
    }
    builder: TreeBuilder;
    programCode: ProgramCodeService;
    framesService: FramesService;
}

/**
 *
 */
export class SmartSkillsPresenterAPI extends CommonPresenterAPI {
    /**
     * Create a new instance of the SmartSkillsPresenterAPI to allow a Presenter to interact with the API and update itself
     */
    constructor(target: UREventTarget | CommunicationChannel, name: string) {
        let communicationChannel;
        if (isCommunicationChannel(target)) {
            communicationChannel = target;
        } else {
            communicationChannel = new EventTargetCommunicationChannel(target as EventTarget);
        }
        super(communicationChannel);
        this.smartSkillInstanceService = new SmartSkillInstanceService(communicationChannel);
        this.robotMoveService = new RobotMoveService(communicationChannel);
        this.freedriveService = new FreedriveService(communicationChannel);
    }
    smartSkillInstanceService: SmartSkillInstanceService;

    /**
     * API for working with robot moves, i.e. bringing up the move screen etc.
     */
    robotMoveService: RobotMoveService;

    freedriveService: FreedriveService;
}
