import { EventTargetOrCommChannel, MessageInvokerService } from '../message-invoker';

export const SNACKBAR_SERVICE_MESSAGE_TYPE_BASE = 'ur.web.message.SnackbarService.';

/**
 * Service that handles showing snack notifications.
 */
export class SnackbarService extends MessageInvokerService {
    /**
     * Create a service for the specific eventTarget
     * @param eventTarget
     */
    constructor(override readonly eventTarget: EventTargetOrCommChannel) {
        super(SNACKBAR_SERVICE_MESSAGE_TYPE_BASE, eventTarget);
    }

    /**
     * Open a snack notification.
     * @param message Snackbar message
     * @param type Optional. Type of notification (success, error, warning, info(default)).
     * @param title Optional. Snackbar title
     */
    showSnackbar(message: string, type?: string, title?: string): Promise<any> {
        return this.postAndAwait('showSnackbar', {
            message,
            type,
            title,
        });
    }
}
