import { Observable } from 'rxjs';
import { RobotSettings } from '../../types/common';
import { EventTargetOrCommChannel, MessageInvokerService } from '../message-invoker';

export const SETTINGS_MESSAGE_TYPE_BASE = 'ur.web.message.SettingsService.';

/**
 * A service for getting settings such as language and units
 */
export class SettingsService extends MessageInvokerService {
    /**
     *
     */
    constructor(override readonly eventTarget: EventTargetOrCommChannel) {
        super(SETTINGS_MESSAGE_TYPE_BASE, eventTarget);
    }

    /**
     * Get the current robot settings as an observable
     */
    getRobotSettings(): Observable<RobotSettings> {
        return this.postAndObserve('getRobotSettings');
    }

    /**
     * Get the current robot settings once
     */
    getRobotSettingsOnce(): Promise<RobotSettings> {
        return this.postAndAwait('getRobotSettingsOnce');
    }
}
