import { ApplicationNode } from '../../types/nodes';
import { EventTargetOrCommChannel, MessageInvokerService } from '../message-invoker';

export const APPLICATION_NODE_SERVICE_MESSAGE_TYPE_BASE = 'ur.web.message.ApplicationNodeService.';

/**
 * Service that handles updating application nodes
 */
export class ApplicationNodeService extends MessageInvokerService {
    /**
     * @constructor
     * @param eventTarget
     */
    constructor(override readonly eventTarget: EventTargetOrCommChannel) {
        super(APPLICATION_NODE_SERVICE_MESSAGE_TYPE_BASE, eventTarget);
    }

    /**
     * Update the program node
     * @param node New program node state
     */
    async updateNode(node: ApplicationNode): Promise<void> {
        return this.postAndAwait('updateNode', node);
    }
}
