/** Describes the various axes used to define poses in the application. */

/** Position */
/**  Constant array of the position axes. */
export const PositionAxes = ['x', 'y', 'z'] as const;
/** Type describing the position axes. */
export type PositionAxis = (typeof PositionAxes)[number];

/** Orientation */
/** Constant array of the orientation axes. */
export const OrientationAxes = ['rx', 'ry', 'rz'] as const;
/** Type describing the orientation axes. */
export type OrientationAxis = (typeof OrientationAxes)[number];

/** Center of Gravity */
/** Constant array of the center of gravity axes. */
export const CenterOfGravityAxes = ['cx', 'cy', 'cz'] as const;
/** Type describing the center of gravity axes. */
export type CenterOfGravityAxis = (typeof CenterOfGravityAxes)[number];

/** Pose */
/** Constant array of the combination of a position and an orientation into a pose. */
export const PoseAxes = [...PositionAxes, ...OrientationAxes] as const;
/** Type describing the combination of a position and an orientation into a pose. */
export type PoseAxis = (typeof PoseAxes)[number];
