import { Observable } from 'rxjs';
import { EventTargetOrCommChannel, MessageInvokerService } from '../message-invoker';

export const PROGRAM_EXECUTION_SERVICE_MESSAGE_TYPE_BASE = 'ur.web.message.ProgramExecutionService.';
/**
 * Service that handles program execution
 */
export class ProgramExecutionService extends MessageInvokerService {
    /**
     * @constructor
     * @param eventTarget
     */
    constructor(override readonly eventTarget: EventTargetOrCommChannel) {
        super(PROGRAM_EXECUTION_SERVICE_MESSAGE_TYPE_BASE, eventTarget);
    }

    /**
     * Stop the program
     */
    async stopProgram(): Promise<void> {
        return this.postAndAwait('stopProgram');
    }

    /**
     * Play the program
     */
    async playProgram(): Promise<void> {
        return this.postAndAwait('playProgram');
    }

    /**
     * Resumes the program
     */
    async resumeProgram(): Promise<void> {
        return this.postAndAwait('resumeProgram');
    }

    /**
     * Pause the program
     */
    async pauseProgram(): Promise<void> {
        return this.postAndAwait('pauseProgram');
    }

    /**
     * Get the Program Status
     */
    getProgramStatus(): Observable<string> {
        return this.postAndObserve('getStatus');
    }

    /**
     * Step to the next line in the program
     */
    async stepProgram(): Promise<void> {
        return this.postAndAwait('stepProgram');
    }
}
