import { MoveScreenOptions } from '../../types/context';
import { JointPositions, Waypoint } from '../../types/nodes';
import { EventTargetOrCommChannel, MessageInvokerService } from '../message-invoker';

export const ROBOT_MOVE_SERVICE_MESSAGE_TYPE_BASE = 'ur.web.message.RobotMoveService.';

/**
 * RobotMoveService handles movement screen navigation
 */
export class RobotMoveService extends MessageInvokerService {
    /**
     * @constructor
     * @param eventTarget
     */
    constructor(override readonly eventTarget: EventTargetOrCommChannel) {
        super(ROBOT_MOVE_SERVICE_MESSAGE_TYPE_BASE, eventTarget);
    }

    /**
     * Opens the move screen and returns the selected position of the robot. Returns undefined if cancel is selected
     * @param options Move screen options
     */
    async openMoveScreen(options: MoveScreenOptions): Promise<Waypoint | undefined> {
        return this.postAndAwait('openMoveScreen', options);
    }

    /**
     * Opens the autoMove screen
     * @param targetPosition Joint vector to move to
     */
    async autoMove(targetPosition: JointPositions | Waypoint): Promise<void> {
        return this.postAndAwait('autoMove', targetPosition);
    }
}
