/**
 * Presets available for Digital In signals
 */
export enum DigitalInPreset {
    NONE,
    START,
    STOP,
    PAUSE,
}

/**
 * Signal to Digital In Presets
 */
export type DigitalInPresets = { [signalID: string]: DigitalInPreset };

/**
 * All presets for a source
 */
export type SourcePresets = {
    digitalInPresets: DigitalInPresets;
};
