import { ApplicationNode, URVariable } from '../..';
import { TabInputModel } from '../../../common';
import { Acceleration, AngularAcceleration, AngularSpeed, Speed, Value } from '../../../values';

export enum MotionProfileMoveType {
    Joint = 'joint',
    Linear = 'linear',
    Process = 'process',
}

export enum MovementType {
    Optimove = 'OptiMove',
    Classic = 'Classic',
}

export interface MotionProfilesNode extends ApplicationNode {
    selectedProfile?: string;
    moveProfiles: {
        joint: Array<MotionProfile>;
        linear: Array<MotionProfile>;
        process: Array<MotionProfile>;
    };
}

export interface MotionProfile {
    isDefault: boolean;
    profile: URVariable;
    parameters: MotionProfileMoveParameters;
}

export interface MotionProfileMoveParameters {
    speedType: MovementType;
    speed: TabInputModel<Speed | AngularSpeed>;
    acceleration: TabInputModel<Acceleration | AngularAcceleration>;
    optiMoveSpeed?: TabInputModel<Value>;
    optiMoveAcceleration?: TabInputModel<Value>;
}
