import { Observable } from 'rxjs';
import { PayloadLimits } from '../../types/context';
import { ControlMode } from '../../types/safety';
import { EventTargetOrCommChannel, MessageInvokerService } from '../message-invoker';

export const SAFETY_SERVICE_MESSAGE_TYPE_BASE = 'ur.web.message.SafetyService.';

/**
 * Service for getting information about Safety
 */
export class SafetyService extends MessageInvokerService {
    /**
     * @constructor
     * @param eventTarget
     */
    constructor(override readonly eventTarget: EventTargetOrCommChannel) {
        super(SAFETY_SERVICE_MESSAGE_TYPE_BASE, eventTarget);
    }

    /**
     * Returns a list of safety IO's
     */
    public getIOs(): Promise<Record<string, any>> {
        return this.postAndAwait('getIOs');
    }

    /**
     * Get the maximum allowed tool force for the robot
     */
    getMaxToolForce(): Promise<number> {
        return this.postAndAwait<number>('getMaxToolForce');
    }

    /**
     * Get the payload limits for the robot
     */
    getPayloadLimits(): Promise<PayloadLimits> {
        return this.postAndAwait<PayloadLimits>('getPayloadLimits');
    }

    /**
     * Get the operational mode of the robot
     */
    getOperationalMode(): Observable<ControlMode> {
        return this.postAndObserve('getOperationalMode');
    }
}
