import { FrameName, TCPName } from '../application';
import { JointPositions, Pose } from './index';

/**
 * A Waypoint defines a position of the robot, with a reference to the frame that it's will be relative to.
 */
export type Waypoint = {
    frame: FrameName; // A frame in the kinematic tree, it should always have 'base' as ancestor
    pose: Pose; // The robot pose relative to given frame
    qNear: JointPositions; // Joint vectors for current robot position
    tcp?: TCPName; // Related TCPName
};

/**
 * A type guard to check if an object is a Waypoint
 */
export function isWaypoint(waypoint: any): waypoint is Waypoint {
    return (
        (waypoint as Waypoint)?.frame !== undefined &&
        (waypoint as Waypoint)?.pose !== undefined &&
        (waypoint as Waypoint)?.qNear !== undefined
    );
}
