import { registerSerializer } from 'threads';
import { ApplicationContextSerializer } from './application-context.serializer';
import { InsertionContextSerializer } from './insertion-context.serializer';
import { ScriptBuilderSerializer } from './script-builder.serializer';
import { ScriptContextSerializer } from './script-context.serializer';
import { ValidationContextSerializer } from './validation-context.serializer';

/**
 * Setup registration of serializers to be used when sending/receiving object across worker boundaries
 */
export function registerSerializers() {
    registerSerializer(ScriptContextSerializer());
    registerSerializer(ValidationContextSerializer());
    registerSerializer(InsertionContextSerializer());
    registerSerializer(ScriptBuilderSerializer());
    registerSerializer(ApplicationContextSerializer());
}
